<template>
    <section>
        <div class='entry' data-v-faheader>
            <form data-v-faheader>
                <div class='entryInputBlock' data-v-faheader>
                    <p data-v-faheader>年</p>
                    <input type="number" name="year" v-model="header.year" data-v-faheader>
                </div>
                <div class='entryInputBlock' data-v-faheader>
                    <p data-v-faheader>月</p>
                    <input type="number" name="month" v-model="header.month" data-v-faheader>
                </div>
                <div class='entryInputBlock' data-v-faheader>
                    <p data-v-faheader>日</p>
                    <input type="number" name="day" v-model="header.day" data-v-faheader>
                </div>
                <div class='entryInputBlock' data-v-faheader>
                    <p data-v-faheader>口座ID</p>
                    <input type="text" name="bank" class="bankInput" v-model="header.bank" data-v-faheader>
                </div>
            </form>
        </div>
    </section>
</template>

<script>
export default {
    name : 'FAheader',

    props : ["header"]
}
</script>

<style scoped>
    .entry[data-v-faheader]{
        border: solid 1px #AAAAAA;
        overflow: hidden;
        padding:10px;
        margin: 20px 0px;
        box-shadow: 0 0 6px gray;
    }

    .entryInputBlock[data-v-faheader]{
        float:left;
        padding:5px;
    }

    input[data-v-faheader]{
        width:80px;
    }
    input.bankInput[data-v-faheader]{
        width:120px;
    }

    p[data-v-faheader]{
        margin-top:10px;
        margin-bottom: 5px;
    }
</style>





